html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

html {
  font-size: 62.5%;
  height: 100%;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAkAAAAJCAYAAADgkQYQAAAAG0lEQVQYlWNgIAx4CQnQQAFBNxCjgJe6CggCAHyzALcK0RCiAAAAAElFTkSuQmCC) repeat;
  background-color: peachpuff;
}

body {
  min-height: 100%;
  text-align: center;
}

body > * {
  margin: 0 auto;
}

h1 { 
  font-size: 5rem;
  font-size: calc(4vh + 2vw);
}

h2 {
  font-size: 3rem;
  font-size: calc(2vh + .5vw);
}

h3,h4 {
  font-size: 2rem;
  font-size: min(2rem,calc(2vh + 1vw));
  text-transform: uppercase;
}

p {
  font-size: 1.5rem;
  font-size: min(2rem,calc(1.5vh + 1.2vw));
  margin: 0;
  display: inline-block;
}

ul {
  padding: 0;
}

ul li {
  list-style-type: circle;
  padding: 10px;
}

.wrapper {
  margin: 0 auto;
  max-width: min(80%,800px);
  background-color: rgba(255,255,255,.05);
  padding: 20px;
  border-radius: 50px;
}

.flexForm {
  margin: 20px auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  max-width: 592px;
}

.flexForm button,input {
  padding: 20px;
  margin: 5px;
  flex: 1 1 100%;
}

.flexForm button {
  max-width: 300px;
}

input, button {
  font-size: 1.5rem;
  font-size: min(2rem,calc(1.5vh + 1.2vw));
  text-align: center;
}

input::placeholder {
  text-transform: lowercase;
}

.listDiv {
  display: grid;
  margin: 0 auto;
  max-width: 800px;
  justify-content: center;
  justify-items: center;
  grid-template-columns: repeat(2,minmax(150px, 1fr))
}

.listDiv ul li {
  list-style-type: none;
}

.displayDefBorder {
  border: 1px solid black;
  border-radius: 10px;
  padding: 20px;
}
